@import url(https://fonts.googleapis.com/css?family=Open+Sans|Quicksand);
*{
    font-family: 'Quicksand', 'Open Sans', sans-serif;
}
.header {
    margin-top: .5rem;
    padding-left: 10px;
  }
  
  .header ul li {
    list-style: none;
    margin: 0;
    padding: .5rem 1rem;
    border-radius: 3px;
  }
  
  .header ul li:hover {
    background-color: #444;
  }
  
  .header a:hover {
    text-decoration: none;
  }
  
  .header h3 a {
    color: white;
  }
  
  .header h3 a:hover {
    color: #00bc8c;
  }

.random-planet {
    height: 180px;
    display: flex;
    padding: 1rem;
    border: 1px solid #444;
    margin-bottom: 1rem;
  }
  
  .random-planet .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 1rem;
  }
  
  .random-planet .list-group {
    margin: 1rem 0 0 1rem;
  }
  
  .random-planet .list-group-item {
    padding: .25rem;
  }
  
  .random-planet .list-group-item .term {
    margin-right: .5rem;
  }
@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  @-webkit-keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  .lds-double-ring {
    position: relative;
  }
  .lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #e57070 transparent #e57070 transparent;
    -webkit-animation: lds-double-ring 2.6s linear infinite;
    animation: lds-double-ring 2.6s linear infinite;
  }
  .lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #a02222 transparent #a02222;
    -webkit-animation: lds-double-ring_reverse 2.6s linear infinite;
    animation: lds-double-ring_reverse 2.6s linear infinite;
  }
  .lds-double-ring {
    width: 160px !important;
    height: 160px !important;
    -webkit-transform: translate(-80px, -80px) scale(0.8) translate(80px, 80px);
    transform: translate(-80px, -80px) scale(0.8) translate(80px, 80px);
  }
.hoba {
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.error-indicator {
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(175, 83, 22);
}


.item-list .list-group-item {
    cursor: pointer;
  }
  
  .item-list .list-group-item:hover {
    background-color: #444;
  }
.person-details {
    display: flex;
    margin-top: 1rem;
    flex-direction: row;
    padding: 1rem;
  }
  
  .person-details .person-image {
    width: 30%;
    height: 30%;
    border-radius: 10px;
  }
  
  .person-details .list-group-item {
    padding: .25rem;
  }
  
  .person-details .list-group-item .term{
    margin-right: .5rem;
  }
