@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  @-webkit-keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  .lds-double-ring {
    position: relative;
  }
  .lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #e57070 transparent #e57070 transparent;
    -webkit-animation: lds-double-ring 2.6s linear infinite;
    animation: lds-double-ring 2.6s linear infinite;
  }
  .lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #a02222 transparent #a02222;
    -webkit-animation: lds-double-ring_reverse 2.6s linear infinite;
    animation: lds-double-ring_reverse 2.6s linear infinite;
  }
  .lds-double-ring {
    width: 160px !important;
    height: 160px !important;
    -webkit-transform: translate(-80px, -80px) scale(0.8) translate(80px, 80px);
    transform: translate(-80px, -80px) scale(0.8) translate(80px, 80px);
  }
.hoba {
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
